.game__head{display:flex;align-items:center;justify-content:center;flex-wrap:wrap;margin-bottom:40px}.game__head .total__poinst{margin:0 25px;padding:5px 20px;font-size:20px;border-radius:5px;border:4px solid rgba(0,0,0,.5);box-shadow:0 0 10px rgba(0,0,0,.5);background-color:#f6f6f6}.game__head .total__poinst span{font-weight:bold;font-size:150%}.game__head .current__points{width:60px;height:60px;font-weight:bold;font-size:22px;line-height:52px;text-align:center;border-radius:50%;border:4px solid rgba(0,0,0,.5);box-shadow:0 0 10px rgba(0,0,0,.5);color:#394689;background-color:#f6f6f6}.game__head .current__answers{display:flex;align-items:center;justify-content:center;margin-top:15px;width:100%}.game__head .current__answers.active .answer__star:before{color:#d27230}.game__head .coefficient__place{width:30px;height:30px;border-radius:50%;font-weight:bold;line-height:30px;font-size:20px;text-align:center;color:#fff;background:#333c54}.game__head .answer__star{width:30px;height:30px;margin:0 4px}.game__head .answer__star:before{content:"";width:100%;height:100%;font-size:40px;line-height:30px}.game__head .answer__star.active:before{color:#0b993a}@media screen and (max-width: 576px){.game__head{margin-bottom:20px}.game__head .total__poinst{margin:0 5px;font-size:18px}.game__head .total__poinst span{font-size:110%}.game__head .current__points{font-size:16px;width:40px;height:40px;line-height:34px}}